@charset "UTF-8";
.Conversation {
  min-width: 250px;
  max-width: 450px;
  width: 100%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%); }
  .Conversation .chatContent {
    border: 1px solid #363946;
    padding: 10px; }
    .Conversation .chatContent h1 {
      font-size: 24px;
      margin-top: 0;
      border-bottom: 1px solid #363946; }
    .Conversation .chatContent .messages {
      max-height: 250px;
      padding-left: 5px;
      padding-right: 5px;
      overflow-y: auto; }
      .Conversation .chatContent .messages > div {
        border: 1px solid #363946;
        margin-bottom: 15px;
        max-width: 70%;
        padding: 10px;
        float: right;
        clear: both;
        position: relative; }
        .Conversation .chatContent .messages > div::before {
          content: ' ⬅️Them';
          font-weight: bold;
          float: right;
          clear: both;
          width: 100%;
          text-align: right;
          margin-bottom: 5px; }
        .Conversation .chatContent .messages > div.outgoing {
          float: left; }
          .Conversation .chatContent .messages > div.outgoing::before {
            content: 'You ➡️ ';
            font-weight: bold;
            float: left;
            text-align: left; }
        .Conversation .chatContent .messages > div .status {
          position: absolute;
          top: 5px;
          left: 0;
          transform: translateX(calc(-100% - 5px)); }
          .Conversation .chatContent .messages > div .status:empty {
            display: none; }
        .Conversation .chatContent .messages > div.outgoing .status {
          left: auto;
          right: 0;
          transform: translateX(calc(100% + 10px)); }
  .Conversation .failedInfo {
    color: red; }
